//import { getOracleServerUrl } from "../shared/utils/index.js";
import axios from "axios";

export const isProdChain = (chainId) => {
  return ["1", "56", "128", "137"].includes(chainId.toString());
};

export const PRESERVED_SYMBOLS = ["BTCUSD", "ETHUSD", "BNBUSD"];

export const getOracleServerUrl = (chainId, symbol, type = "futures") => {
  //if (/^[0-9]+$/.test(symbolId.toString())) {
  let method = "get_signed_price";
  if (type === "option") {
    method = "get_signed_volatility";
  }
  if (PRESERVED_SYMBOLS.includes(symbol)) {
    method = "get_price";
    symbol = `${symbol}_v2_bsc`;
  }
  let baseUrl = isProdChain(chainId)
    ? `https://oraclemainnet.deri.io/${method}`
    : `https://oracletestnet.deri.io/${method}`;
  const addSymbolParam = (url, symbol) => `${url}?symbol=${symbol}`;

  if (symbol) {
    return addSymbolParam(baseUrl, symbol);
  } else {
    return baseUrl;
  }
};

const addSymbolParam2 = (url, symbol) => `${url}?symbols=${symbol}`;
const getOracleServerUrl2 = (symbolList, isSigned) => {
  //if (/^[0-9]+$/.test(symbolId.toString())) {
  let method = isSigned ?  "signedv2" : "unsigned"
  let baseUrl = `https://sig.oraclum.io/${method}`

  return addSymbolParam2(baseUrl, symbolList.join(','))
};


export const getPriceInfos = async (chainId, symbolList) => {
  if (Array.isArray(symbolList) && symbolList.length > 0) {
    let url = getOracleServerUrl2(symbolList, true);
    const res = await axios.get(url);
    if (res.status === 200) {
      const priceInfo = res.data;
      if (Array.isArray(priceInfo) && priceInfo.length > 0) {
        return priceInfo.reduce((acc, p) => {
          acc[p.symbol] = p;
          return acc;
        }, {});
      }
    }
  } else {
    return []
  }
};
