import { useEffect, useState, useRef } from 'react';
import Icon from "../Icon/Icon"
import { inject, observer } from 'mobx-react'
import { formatAddress } from "../../utils/utils"
import Emitter from "../../utils/Emitter";
import { EVENT_TRANS_BEGIN, EVENT_TRANS_END } from '../../utils/Constants'
import './transactionState.less'
import Loading from '../LiquidityGuidance/Loading'
function TransactionState({ wallet, trading }) {
  const [isShow, setIsShow] = useState(false)
  const [messageInfo, setMessageInfo] = useState({})
  const [isShowError, setIsShowError] = useState(false)
  const endTag = useRef()
  const closeBox = () => {
    setIsShow(false);
    setMessageInfo({});
    endTag.current = false
  }
  const onMessageInfoBgein = (data) => {
    let info = {}
    if (endTag.current) {
      endTag.current = false
      return;
    }
    setIsShow(true)
    info["link"] = `${wallet.blockExploreUrl}/tx/${data.hash}`
    info["hash"] = data.hash
    info["title"] = data.title.processing
    info["content"] = data.content.success
    info["type"] = "processing"
    setMessageInfo(info)
  }
  const onMessageInfoEnd = (res) => {
    let info = {}
    if (!res.context) {
      closeBox()
      return false;
    }
    const hash = res.context.hash
    endTag.current = true
    if (res.context.success) {
      info["type"] = "success"
      info["content"] = res.content.success
      info["title"] = res.title.success
      info["hash"] = hash
      info["link"] = `${wallet.blockExploreUrl}/tx/${hash}`
      setMessageInfo(info)
    } else {
      if (hash === "") {
        closeBox()
        info["type"] = "error"
        info["title"] = res.title.error
        info["error"] = res.context.error
        info["content"] = res.content.error
        setMessageInfo(info)
        setIsShow(true)
        return false;
      }
      info["type"] = "error"
      info["title"] = res.title.error
      info["content"] = `${res.content.error} : ${res.context.error}`
      info["hash"] = hash
      info["link"] = `${wallet.blockExploreUrl}/tx/${hash}`
      setMessageInfo(info)
    }
    setIsShow(true)
  }
  useEffect(() => {
    Emitter.on(EVENT_TRANS_BEGIN, onMessageInfoBgein)
    Emitter.on(EVENT_TRANS_END, onMessageInfoEnd)
    return () => {
      Emitter.off(EVENT_TRANS_BEGIN, onMessageInfoBgein)
      Emitter.off(EVENT_TRANS_END, onMessageInfoEnd)
    }
  }, [])
  useEffect(() => {
    let timer
    if (messageInfo.type === "success") {
      timer = window.setTimeout(() => {
        closeBox();
        console.log("closeBox")
      }, 8000)
    } else {
      timer = window.setTimeout(() => {
        endTag.current = false
      }, 2000)
    }
    return () => {
      timer && clearTimeout(timer)
    }
  }, [messageInfo])
  return (
    <>
      {isShow ? <div className='transaction-state'>
        <div className='message-title'>
          <div className='icon message-title-text'>
            {messageInfo.type === 'success' && <Icon token='success' width={16} height={16} />}
            {messageInfo.type === 'error' && <Icon token='error' width={16} height={16} />}
            {messageInfo.type === 'processing' && <Loading borderColor="rgba(85, 119, 253, 1)" bgColor="rgba(55, 125, 255, 0.1)" />}
            {messageInfo.title}
          </div>
          <div className={messageInfo.type !== 'success' ? 'close noAnimation' : 'close'} onClick={closeBox}>
            <div className='close-icon'>
              <Icon token="close-modal-icon" width='7' />
            </div>
            <div className={messageInfo.type === 'success' ? "circle_process animation" : "circle_process"}>
              <div className="wrapper right">
                <div className="circle rightcircle"></div>
              </div>
              <div className="wrapper left">
                <div className="circle leftcircle" id="leftcircle"></div>
              </div>
            </div>
          </div>
        </div>
        <div className='message-text-link'>
          <div className='message-text-box'>
            <div className='message-text'>
              {messageInfo.content}
            </div>
            <div className={messageInfo.type === 'success' ? 'link' : messageInfo.type === "error" ? 'link error' : "link processing"}>
              {messageInfo.hash && <a rel='noreferrer' target='_blank' href={messageInfo.link}>Click here to view transaction {formatAddress(messageInfo.hash)}</a>}
              {!messageInfo.hash && <div className='message-no-hash'>
                <div className="is-show-error" onClick={() => setIsShowError(!isShowError)}>
                  {!isShowError ? "Show error" : "Hide error"}
                </div>
                {isShowError ? <div className="no-hash-message">
                  {messageInfo.error}
                </div> : null}
              </div>}
            </div>
          </div>

        </div>
      </div> : null}
    </>

  )
}
export default inject('wallet', "trading")(observer(TransactionState))