export const getRetiredSymbolsInfo = async (chainId, accountAddress) => {
  return catchApiError(
    async () => {
      chainId = checkChainId(chainId);
      accountAddress = checkAddress(accountAddress);
      const retiredPool =
        DeriEnv.get() === "prod"
          ? {
              pool: "0xD2D950e338478eF7FeB092F840920B3482FcaC40",
              symbols: ["ETH^2", "BTC^2"],
            }
          : null;
      if (retiredPool && retiredPool.pool) {
        const api = poolApiFactory(chainId, retiredPool.pool);
        await api.init(accountAddress);
        return await api.getRetiredPositionsInfo(
          accountAddress,
          retiredPool.symbols
        );
      } else {
        return [];
      }
    },
    [],
    []
  );
};

export const getPoolOnChainApy = async (chainId, poolAddress) => {
  let res = '0'
  try {
    const rewardVault = rewardVaultFactory(chainId, getRewardVaultConfig(chainId, poolAddress).rewardVault)
    const pool = poolImplementationFactory(
      chainId,
      poolAddress,
    )
    const [deriInfo, rewardPerSecond] = await Promise.all([
      fetchJson('https://infoapi.deri.io/stats_for_token'),
      rewardVault.rewardPerSecond(),
      pool.init(),
    ])
    if (deriInfo.status.toString() === '200') {
      const deriPrice = deriInfo.data.price
      // console.log(rewardPerSecond, deriPrice, pool.state.liquidity)
      res = bg(rewardPerSecond).times(60 * 60 * 24 * 365).times(deriPrice).div(pool.state.liquidity).toString()
    }
  } catch (err) {
    console.log(err)
  }
  return res
}

export const getPoolMiningInfo = async (chainId, poolAddress) => {
  return catchApiError(
    async () => {
      chainId = checkChainId(chainId);
      poolAddress = checkAddress(poolAddress);
      const url = `${getHttpBase(
        DeriEnv.get()
      )}/pool_mining_info/${chainId}/${poolAddress}`;
      const res = await axios.get(url);
      if (res.status === 200) {
        const data = res.data.data;
        if (data.apy.indexOf(':') > -1) {
          const [apyB0, apyBX] = data.apy.split(':')
          return { ...data, apyB0, apyBX, apy: apyB0 };
        } else {
          return data
        }
      }
      throw new Error(`fetch pool_mining_info error`)
    },
    [],
    {}
  );
};