import { io } from "socket.io-client";

class WebSocket {
  constructor(url,options = {}){
    this.socket = io(url, {
      transports: ['websocket'],
      withCredentials: true,
      reconnection : true,
      reconnectionDelay : 500,
      reconnectionAttempts : 10
    })
    const {onConnected,onDisconnect,onReconnect,onConnectError} = options
    this.onReconnect = onReconnect
    this.reconnected = false
    this.socket.on('connect', () => {
      onConnected && onConnected()
      if(this.reconnected){
        this.reconnected = false
        this.onReconnect && this.onReconnect()
      }
    })
    this.socket.on('disconnect',event => {
      this.reconnected = true
      onDisconnect && onDisconnect()
      console.log('web socket disconnect,will reconnect auto')

    })
    this.socket.on('connect_error', function(err) {
      onConnectError && onConnectError()
      console.log('Error connecting to server',err);
    });
  }

  onRecconnect(callback){
    this.onReconnect = callback
  }

  connect(){
    this.socket.connect();
  }

  disconnect(){
    this.reconnected = false;
    this.socket.disconnect()
  }

  isConnected(){
    this.socket.connected
  }

  subscribe(event = 'update',params = {},onMessage,listener = 'update',kline = true){
    if(!this.isConnected()){
      this.connect()
    }
    const reg = new RegExp(`${params.type}`,'i')
    this.socket.on(listener,data => {
      if (kline) {
        if(onMessage && reg.test(data.id)){
          onMessage(data)
        }
      } else {
        onMessage(data)
      }
    })
    this.socket.emit(event, params)
  }

  unsubscribe(event,params = {},cb){
    this.socket.emit(event,params,() => {
      cb && cb() ;
      console.info(`unsubscribe event : ${event} successful`)
    })
  }
}
export default WebSocket