import {useEffect} from 'react'
import { getChainProviderUrlsFromRest } from '../lib/web3js';

const WHITE_LIST_STORAGE_KEY = 'provider_map'

export default function useRpcProvider(){

  const fetcher = async () => {
    const whiteList = await getChainProviderUrlsFromRest()
    if(whiteList && Object.keys(whiteList).length > 0){      
      localStorage.setItem(WHITE_LIST_STORAGE_KEY,JSON.stringify(whiteList))
    }
  }

  useEffect(async () => {
    const interval = window.setInterval(async () => {
        fetcher()
    },1000 * 60 * 5)
    fetcher();
    return () => { window.clearInterval(interval)}
  }, [])
  return localStorage.getItem(WHITE_LIST_STORAGE_KEY);
}