import { getPositionInfo, bg, getPositionInfos, DeriEnv } from "../lib/web3js/index"
import { eqInNumber, getDefaultNw, greaterThan } from '../utils/utils';
import type from "./Type";
import ApiProxy from "./ApiProxy";
import { makeObservable, observable, action } from "mobx";
import { VERSION_V3, VERSION_V2_LITE, SHORT_NAME_MAP } from "../utils/Constants";

export default class Position {

  callback = () => { }
  callbackALL = () => { }
  wallet = null;
  symbolInfo = null


  async load(wallet, symbolInfo, callback) {
    this.wallet = wallet;
    this.symbolInfo = symbolInfo
    let position = {}
    if (callback) {
      this.callback = callback
    }
    if (symbolInfo && symbolInfo.address && wallet.supportCurNetwork) {
      const chainId = wallet && wallet.detail ? wallet.detail.chainId : getDefaultNw(DeriEnv.get()).id
      const account = wallet && wallet.detail ? wallet.detail.account : null;
      const symbolParam = symbolInfo.isAllV3 ? symbolInfo.symbol : symbolInfo.symbolId
      position = await ApiProxy.request('getPositionInfo', [chainId, symbolInfo.address, account, symbolParam], { debugger: false })
    }
    if (this.callback) {
      this.callback(position)
    }
    return position
  }

  async loadAll(wallet, symbolInfo, callback) {
    this.wallet = wallet;
    this.symbolInfo = symbolInfo
    let positions = []
    if (callback) {
      this.callbackALL = callback
    }
    const chainId = wallet && wallet.detail.chainId
    const account = wallet && wallet.detail.account
    if (symbolInfo && symbolInfo.address && wallet.supportCurNetwork && chainId && account) {
      const symbolParam = symbolInfo.isAllV3 ? symbolInfo.symbol : symbolInfo.symbolId
      positions = await ApiProxy.request('getPositionInfos', [chainId, symbolInfo.address, account, symbolParam], { debugger: false })
      if (positions.length) {
        positions = positions.map(item => this.wrapperForDisplay(item, symbolInfo))
      }
    }
    if (this.callbackALL) {
      this.callbackALL(positions)
    }
    return positions
  }

  wrapperForDisplay(item, symbolInfo) {
    item.balanceContract = bg(item.margin).plus(item.unrealizedPnl).toString()
    item.direction = greaterThan(item.volume, 0) ? 'Long' : (!item.volume || eqInNumber(item.volume, 0) || !item.volume ? '--' : 'Short')
    item.formatedSymbol = item.bTokenLength === 1 ? `${item['displaySymbol']} / ${item['b0Unit']}` : item['displaySymbol'] || item['symbol']
    if (item.direction === 'Long') {
      item.directionColor = 'green'
    } else if (item.direction === 'Short') {
      item.directionColor = 'red'
    }
    if (greaterThan(item.unrealizedPnl, 0)) {
      item.unrealizedPnlColor = 'green'
    } else {
      item.unrealizedPnlColor = 'red'
    }

    if (greaterThan(item.fundingFee, 0)) {
      item.fundingFeeColor = 'green'
    } else {
      item.fundingFeeColor = 'red'
    }
    if (item.volume !== "0") {
      let unit = item.displaySymbol[0]
      let multiplier = SHORT_NAME_MAP[unit]
      if (multiplier) {
        item.displayVolume = bg(item.volume).div(multiplier).toString()
      }
      item.notional = bg(Math.abs(item.displayVolume)).times(item.category === "option" ? item.price : item.displayMarkPrice).toString();
      item.notionalColor = 'gray'
    }
    item.displayNotionalUnit = 'USD'
    if(item.b0Unit && item.b0Unit === 'DERI') {
      item.displayNotionalUnit = item.b0Unit
    }
    if (item.markPrice && item.averageEntryPrice) {
      // if(greaterThan(item.volume,0)) {
      //   item.unrealizedPnlRate = bg(item.markPrice).minus(item.averageEntryPrice).div(item.averageEntryPrice).times(100).toString();
      // } else {
      //   item.unrealizedPnlRate = bg(item.averageEntryPrice).minus(item.markPrice).div(item.averageEntryPrice).times(100).toString();
      // }
      let unit = item.displaySymbol[0]
      let multiplier = SHORT_NAME_MAP[unit]
      if (multiplier) {
        item.markPrice = bg(item.markPrice).times(multiplier).toString()
        item.averageEntryPrice = bg(item.averageEntryPrice).times(multiplier).toString()
      }
      item.unrealizedPnlRate = bg(item.unrealizedPnl).div(Math.abs(item.cost)).times(100).toString();
      item.unrealizedPnlRateColor = 'gray'
    }
    item['fundingFee'] = -item['fundingFee']
    const symboleInArray = item.symbol.split('-')
    item['isOptions'] = symboleInArray.length > 1 ? true : false
    item['underlier'] = symboleInArray[0]
    return item;
  }

  startAll() {
    this.paused = false;
    if (this.positionsTimer) {
      window.clearTimeout(this.positionsTimer)
    }
    this.positionsTimer = this.runInAction(async () => await this.loadAll(this.wallet, this.symbolInfo))
  }

  start() {
    this.paused = false;
    if (this.positionTimer) {
      window.clearTimeout(this.positionTimer)
    }
    this.positionTimer = this.runInAction(async () => await this.load(this.wallet, this.symbolInfo))
  }

  runInAction(action) {
    let timer
    const doAction = async () => {
      if (!this.paused) {
        const position = await action();
        if (timer) {
          clearTimeout(timer)
        }
        if (position) {
          timer = window.setTimeout(doAction, 3000)
        }
      } else {
        return timer;
      }
    }
    return window.setTimeout(doAction, 3000)
  }

  pause() {
    if (!this.paused) {
      this.paused = true
    }
  }

  resume() {
    if (this.paused) {
      this.paused = false
      // this.start(this.callback)
      this.startAll(this.callbackALL)
    }
  }

  clean() {
    this.started = false;
    this.startedAll = false;
    this.paused = true;
    if (this.positionTimer) {
      window.clearTimeout(this.positionTimer)
    }
    if (this.positionsTimer) {
      window.clearTimeout(this.positionsTimer)
    }
    this.positionsTimer = this.positionTimer = null;
  }

}