import { ADDRESS_ZERO, checkApiInput } from '../../shared/config';
import { DeriEnv } from '../../shared/config/env';
import { bg, catchApiError, fromWei } from '../../shared/utils';
import { getVenusControllerConfig, isBSCChain } from '../config';
import { poolImplementationFactory } from '../contract/factory/pool';
import {
  vBEP20Factory,
  vBNBFactory,
  venusControllerFactory,
} from '../contract/factory/rest';
import { executeAsync } from '../utils/batch';
import { getWeb3 } from '../utils/web3';

//
// refer https://github.com/VenusProtocol/venus-protocol-interface/blob/master/src/containers/Main/Vote.js#L117-L170
//
export const getVenusEarned = async (
  chainId,
  poolAddress,
  accountAddress,
  opts = {}
) => {
  return catchApiError(async () => {
    [chainId, poolAddress, accountAddress] = checkApiInput(
      chainId,
      poolAddress,
      accountAddress
    );
    if (isBSCChain(chainId)) {

      opts.asLp = opts.asLp || true;
      opts.asTrader = opts.asTrader || false;
      const pool = poolImplementationFactory(chainId, poolAddress);
      await pool.init(accountAddress);
      const venusControllerConfig = getVenusControllerConfig(DeriEnv.get(), chainId)
      const controller = venusControllerFactory(
        chainId,
        venusControllerConfig.address
      );
      let vaultAddress = !opts.asTrader
        ? opts.asLp
          ? pool.account.lpVault
          : ADDRESS_ZERO
        : pool.account.tdVault;
      let venusEarned = bg(0);
      const [venusAccrued, venusInitialIndex] = await Promise.all([
        controller.venusAccrued(vaultAddress),
        controller.venusInitialIndex(),
      ]);
      if (vaultAddress === ADDRESS_ZERO) {
        return '0';
      }
      await Promise.all(
        pool.bTokens.map(async (b) => {
          const vToken = ['vBNB', 'VBNB'].includes(b.vTokenSymbol)
            ? vBNBFactory(chainId, b.vTokenAddress)
            : vBEP20Factory(chainId, b.vTokenAddress);
          await vToken._init()
          // let [
          //   supplyState,
          //   supplierIndex,
          //   supplierTokens,
          //   borrowState,
          //   borrowerIndex,
          //   borrowBalanceStored,
          //   borrowIndex,
          // ] = await Promise.all([
          //   controller.venusSupplyState(b.vTokenAddress),
          //   controller.venusSupplierIndex(b.vTokenAddress, vaultAddress),
          //   vToken.balanceOf(vaultAddress),
          //   controller.venusBorrowState(b.vTokenAddress),
          //   controller.venusBorrowerIndex(b.vTokenAddress, vaultAddress),
          //   vToken.borrowBalanceStored(vaultAddress),
          //   vToken.borrowIndex(vaultAddress),
          // ]);
          // change to batch request
          const web3 = await getWeb3(chainId)
          let batch = new web3.BatchRequest()
          batch.add(controller.contract.methods.venusSupplyState(b.vTokenAddress).call.request())
          batch.add(controller.contract.methods.venusSupplierIndex(b.vTokenAddress, vaultAddress).call.request())
          batch.add(vToken.contract.methods.balanceOf(vaultAddress).call.request())
          batch.add(controller.contract.methods.venusBorrowState(b.vTokenAddress).call.request())
          batch.add(controller.contract.methods.venusBorrowerIndex(b.vTokenAddress, vaultAddress).call.request())
          batch.add(vToken.contract.methods.borrowBalanceStored(vaultAddress).call.request())
          batch.add(vToken.contract.methods.borrowIndex().call.request())
          let [
            supplyState,
            supplierIndex,
            supplierTokens,
            borrowState,
            borrowerIndex,
            borrowBalanceStored,
            borrowIndex,
          ] = await executeAsync(batch)
          // transform
          supplyState.index = fromWei(supplyState.index)
          supplierIndex = fromWei(supplierIndex)
          supplierTokens = fromWei(supplierTokens)
          borrowState.index = fromWei(borrowState.index)
          borrowerIndex = fromWei(borrowerIndex)
          borrowBalanceStored = fromWei(borrowBalanceStored)
          borrowIndex = fromWei(borrowIndex)

          const supplyIndex = supplyState.index;
          if (+supplierIndex === 0 && +supplyIndex > 0) {
            supplierIndex = venusInitialIndex;
          }
          let deltaIndex = bg(supplyIndex).minus(supplierIndex);
          const supplierDelta = bg(supplierTokens).times(deltaIndex).div(1e18);
          venusEarned = venusEarned.plus(supplierDelta);

          if (+borrowerIndex > 0) {
            deltaIndex = bg(borrowState.index).minus(borrowerIndex);
            const borrowerAmount = bg(borrowBalanceStored).div(borrowIndex);
            const borrowerDelta = borrowerAmount.times(deltaIndex).div(1e18);
            venusEarned = venusEarned.plus(borrowerDelta);
          }
          //console.log(b.bTokenSymbol, venusEarned.toString())
        })
      );
      venusEarned = venusEarned.plus(venusAccrued).toString();
      return venusEarned;
    }
  }, [chainId, opts], '0');

};
