import styled,{keyframes} from 'styled-components'
import Icon from '../Icon/Icon';

const Wrapper = styled.div`
    &.anim-circle {
      color: transparent;
      width: 50px;
      height: 50px;
      border-radius: 50%;
      background: rgba(${props => props.rgb},0.983);
      animation: ${props => breathe(props.rgb)} 4s linear infinite;
      display : flex;
      justify-content: center;
      align-items: center;
    }    
  `
  const breathe = color => keyframes`
      0% {
        box-shadow: 
          0 0 0 4px rgba(${color},0.3),
          0 0 0 8px rgba(${color},0.3),
          0 0 0 12px rgba(${color},0.1);
      }
      50% {
        box-shadow: 
          0 0 0 8px rgba(${color},0.3), 
          0 0 0 16px rgba(${color},0.3), 
          0 0 0 22px rgba(${color},0.1);
      }
      100% {
        box-shadow: 
          0 0 0 4px rgba(${color},0.3), 
          0 0 0 8px rgba(${color},0.3),
          0 0 0 12px rgba(${color},0.1);
      }`;
export default function BreatheIcon({rgb,icon}) {
  return(
    <Wrapper className='anim-circle' rgb={rgb}>
      <Icon token={icon} width='22' height='22'/>
    </Wrapper>
  )
}