import Header from './components/Header/Header';
import Footer from './components/Footer/Footer';
import Announcement from './components/Announcement/Announcement'
import AnnouncementArbitrum from './components/Announcement/AnnouncementArbitrum'
import Welcome from './components/Welcome/Welcome';
import TransactionState from './components/TransactionState/TransactionState'
import Intro from './components/Intro/Intro'
import PageRouter from './pages/PageRouter';
import { useLocation } from 'react-router-dom'
import { DeriEnv } from './lib/web3js/index'
import './app.less';
import './assets/css/fonts.less'
import classNames from 'classnames';
import { show } from 'react-functional-modal'
import loadingImg from './assets/img/loading.png'
import React, { useEffect, useState } from 'react'
import { inject, observer } from 'mobx-react';
import { getCookie, setCookie } from './utils/utils';
import close from './assets/img/close-modal-icon.svg'
import useRpcProvider from './hooks/useRpcProvider';
import { useWallet } from 'use-wallet';
function App() {
  const location = useLocation();
  useRpcProvider()
  const curRouterClass = location.pathname.split('/')[1]
  const trade = location.pathname.split('/')[2]
  const rootClass = classNames('deri-root', curRouterClass, trade)
  return (
    <div className={rootClass}>
      <Announcement />
      <Intro trade={trade} />
      <TransactionState />
      <Header></Header>
      <PageRouter></PageRouter>
    </div>
  );
}

export default App
