import {
  getChainProviderUrls,
  getDailyBlockNumberConfig,
  chainConfigList,
} from '../config/chain';
import { getLatestRPCServer, shuffleArray } from './network';
import { AppError } from '../error/error';
import axios from "axios"
import { getHttpBase } from './api';
import { checkChainId } from '../config';

export const getChainProviderUrlsFromRest = async (chainId='all') => {
  try {
    if (chainId !== 'all') {
      chainId = checkChainId(chainId)
    }
    const res = await axios.get(`${getHttpBase()}/provider_urls/${chainId}`)
    if (res.status === 200 && res.data.success === true) {
      return res.data.data.reduce((acc, d) => {
        if (!acc[d.chainId]) {
          acc[d.chainId] = [d.url]
        }else {
          acc[d.chainId].push(d.url)
        }
        return acc
      }, {})
    }
  } catch(err) {
    console.log(err)
  }
  return {}
}

export const getChainProviderUrls2 = (chainId) => {
  let urls = []
  try {
    chainId = checkChainId(chainId)
    const data = localStorage.getItem('provider_map');
    if (typeof data === 'string' && data.length > 2) {
      const configs = JSON.parse(data)
      if (
        Array.isArray(configs[chainId]) &&
        configs[chainId].length > 0 &&
        typeof configs[chainId][0] === "string"
      ) {
        // prevent user change the provider_map value
        urls = configs[chainId];
      }
    }
  } catch (err) {
    console.log(err)
  }
  if (Array.isArray(urls) && urls.length === 0) {
    urls = getChainProviderUrls(chainId);
  }
  return urls
}

export const getChainProviderUrl = async (chainId) => {
  let urls = getChainProviderUrls2(chainId);
  if (urls.length > 1) {
      urls = shuffleArray(urls)
    if (urls.length > 3) {
      urls = urls.slice(0,3)
    }
   // const url =  await getLatestRPCServer(urls);
   const url = urls[0]
   // console.log('url', url)
   return url
  } else if (urls.length === 1) {
    return urls[0]
  } else {
    throw new Error(
      `Cannot find the chain provider url with chainId: ${chainId}`
    );
  }
};

export const getDailyBlockNumber = (chainId) => {
  const blockNumbers = getDailyBlockNumberConfig();
  if (blockNumbers[chainId]) {
    return parseInt(blockNumbers[chainId]);
  } else {
    throw new Error(`Invalid annual block number with chainId: ${chainId}`);
  }
};

export const getNetworkName = (chainId) => {
  const config = chainConfigList.find((c) => c.chainId === chainId)
  if (config && config.name)  {
    return config.name
  }
  throw new AppError('CONFIG_NOT_FOUND', {
    name: 'getNetworkName',
    args: [chainId],
  });
};

export const getChainGasUnit= (chainId) => {
  const config = chainConfigList.find((c) => c.chainId === chainId)
  if (config && config.unit)  {
    return config.unit
  }
  throw new AppError('CONFIG_NOT_FOUND', {
    name: 'getChainGasUnit',
    args: [chainId],
  });
};