import './welcome.less'
import deri from './img/deri.svg'
import arbitrum from './img/arbitrum.svg'
import { hide } from 'react-functional-modal'
import { setCookie } from '../../utils/utils'
export default function Welcome() {
  const hideWelcome = () => {
    setCookie('welcome-lite', 'false')
    hide('Welcome')
  }
  return (
    <>
      <div className='welcome-arbitrum'>
        <div className='welcome-box'>
          <div className='welcome-logo'>
            <div className='deri-logo'>
              <img src={deri} alt="deri" />
            </div>
          </div>
          <div className='welcome-title'>
            🎉
            <div className='title-two'>
              Deri Lite Launch
            </div>
          </div>
          <div className='welcome-info-box'>
            Enjoy instant trading experiences on the brand new Deri Lite!
          </div>
          <div className='welcome-info'>
            <ul>
              <li>
                User-centered design
              </li>
              <li>
                Optimized trading experience
              </li>
              <li>
                Isolated margin mode
              </li>
              <li>
                up to 25x leverage
              </li>
            </ul>
          </div>

          <div className='learn-more-btn'>
            <a onClick={() => { window.open("https://lite.deri.io/") }}>GO TO DERI LITE</a>
            <div className='btn-ok' onClick={hideWelcome}>
              OK
            </div>
          </div>
        </div>
      </div>
    </>
  )
}