import { bg, fromWei } from "../../shared/utils";
import { isOptionSymbol, onChainSymbols } from "../config";
import { calculateDpmmCost } from "../utils";
import { getSymbolsOracleInfo } from "../utils/oracle";

export const sendTxWithOracleSignature = async (
  chainId,
  symbol,
  symbols,
  fn,
  args,
  calcPriceLimit = false,
) => {
  const opts = args.pop()
  const signedPrices = await getSymbolsOracleInfo(chainId, symbols)
  const signedPriceInfo = signedPrices.find((s) => s[0] === symbol.symbolId)
  if (calcPriceLimit && signedPriceInfo) {
    let newVolume = args[2]
    let limitedPercent = args.pop()
    let price = fromWei(signedPriceInfo[2])

    const tradePrice = calculateDpmmCost(
      price,
      symbol.K,
      symbol.netVolume,
      newVolume
    )
      .div(newVolume)
      .toString();
    const priceLimit = bg(newVolume).gte(0)
      ? bg(tradePrice)
          .plus(bg(limitedPercent).times(symbol.markPrice))
          .toString()
      : bg(tradePrice)
          .minus(bg(limitedPercent).times(symbol.markPrice))
          .toString();
    args[3] = priceLimit;
  }
  if (args.length > 4) {
    args.pop()
  }
  const newArgs = [...args, signedPrices, opts];
  return await fn(...newArgs);
};
