import { errors } from 'web3-core-helpers'

export function executeAsync(batch) {
  return new Promise((resolve, reject) => {
    var requests = batch.requests;

    batch.requestManager.sendBatch(requests, (err, results) => {
      results = results || [];

      var response = requests
        .map((request, index) => {
          return results[index] || {};
        })
        .map((result, index) => {
          if (result && result.error) {
            return errors.ErrorResponse(result);
          }
          // if (!pkg.Jsonrpc.isValidResponse(result)) {
          //   return errors.InvalidResponse(result);
          // }

          return requests[index].format
            ? requests[index].format(result.result)
            : result.result;
        });

      resolve(response);
    });
  });
}
