import Web3 from 'web3';
import { normalizeChainId } from '../utils/validate';
import { getChainProviderUrl } from '../utils/chain';
import { isBrowser, isJsDom } from '../utils/convert';
import { DEBUG } from '../config';

const delay = (ms) => new Promise(resolve => setTimeout(resolve, ms))

export const web3Factory = (function () {
  const web3InstanceMap = {};
  let pending = {}
  let walletChainId = null
  return {
    async get(chainId, isTx = false) {
      chainId = normalizeChainId(chainId)
      const key = `${chainId}_${isTx}`
      if (web3InstanceMap[key]) {
        return web3InstanceMap[key];
      }
      // for mining page should not depends wallet network
      if (isTx) {
        if (window && typeof window.ethereum === 'object') {
          try {
            walletChainId = await window.ethereum.request({
              method: 'eth_chainId',
            });
          } catch (err) {
            console.log(err);
          }
          // walletChainId = window.ethereum.chainId;
          if (walletChainId && typeof walletChainId !== 'string') {
            walletChainId = walletChainId.toString()
          }
          if (walletChainId && walletChainId.startsWith('0x')) {
            walletChainId = Web3.utils.hexToNumberString(walletChainId);
          }
        }
      if (window && window.ethereum && chainId === walletChainId) {
        const web3 = new Web3(window.ethereum)
        web3.currentProvider.setMaxListeners(300);
        return web3;
      } else if (window && window.ethereum) {
        throw new Error(
          `invalid reqeust chainId(${chainId}) with connected wallet chainId(${chainId})`
        );
      }
    }
      // using metaMask ethereum object
      if (pending[key] || web3InstanceMap[key]) {
        // wait for init
        let retry = 8 + Math.ceil(Math.random() * 8);
        while (retry > 0) {
          if (web3InstanceMap[key]) {
            // DEBUG &&  console.log(`-- web3 factory pending: using cached ${web3InstanceMap[chainId]}`)
            return web3InstanceMap[key];
          } else {
            retry -= 1;
          }
          await delay(500);
        }
        // timeout
        const providerUrl = await getChainProviderUrl(chainId);

        // DEBUG &&  console.log(`-- web3 factory pending: using ${providerUrl} `)
        web3InstanceMap[key] = new Web3(
          new Web3.providers.HttpProvider(providerUrl)
        );
        return web3InstanceMap[key];
      } else {
        // first init
        pending[key] = true;
        try {
          const providerUrl = await getChainProviderUrl(chainId);
          DEBUG &&
            console.log(`-- web3 factory: using provider ${providerUrl} `);
          web3InstanceMap[key] = new Web3(
            new Web3.providers.HttpProvider(providerUrl)
          );
        } catch (err) {
          console.log(`-- web3 factory: `, err.toString());
        } finally {
          delete pending[key];
        }
        return web3InstanceMap[key];
      }
    },
    clean(chainId, isTx = false) {
      const key = `${chainId}_${isTx}`
      if (web3InstanceMap[key]) {
        delete web3InstanceMap[key];
      }
    }
  };
})();