import React, { useEffect, useRef, useState, useCallback, useMemo } from 'react'
import Label from '../Label/Label';
import './menu.less'
import ReactTooltip from 'react-tooltip';
import Icon from '../Icon/Icon';
import { DeriEnv } from '../../lib/web3js/index'
import Content from '../Content/Content';
import Tooltip from '../Tooltip/Tooltip';
import { Link } from 'react-router-dom';
import { isSingleDomain } from '../../utils/utils';
import UnderlineText from "../UnderlineText/UnderlineText"
import { inject, observer } from 'mobx-react';
function Menu({ lang, closeSideMenu, locale, wallet }) {
  const Env = DeriEnv.get()
  const moreMenuRef = useRef(null);
  const docsMenuRef = useRef(null)
  const options = { id: 'options', text: lang['options'], link: `https://deri.io/#/trade/options` }
  const powers = { id: 'powers', text: lang['powers'],  link: `https://deri.io/#/trade/powers` }
  let menus = useMemo(() => (
    [
      { id: 'pools', text: lang['pools'], link: `https://deri.io/#/pool` },
      { id: 'Futures', text: lang['Futures'], link: `https://deri.io/#/trade/futures` },
      { ...options },
      { ...powers },
      { id: 'bridge', text: lang['bridge'], link: `https://deri.io/#/bridge` },
      {
        id: 'docs', text: lang['docs'], ref: docsMenuRef, subMenu: [
          { id: 'white-paper', text: lang['white-paper'], link: 'https://docs.deri.io/whitepaper' },
          { id: 'code-audit', text: lang['code-audit'], link: 'https://docs.deri.io/code-audits' },
          { id: 'team', text: lang['team'], link: `https://deri.io/#/team` },
          { id: 'faq', text: lang['faq'], link: 'https://docs.deri.io/library/faqs' },
          { id: 'guide', text: lang['guide'], link: 'https://docs.deri.io/' },
          { id: 'github', text: lang['github'], link: 'https://github.com/deri-protocol' }
        ]
      },
      {
        id: 'more', text: lang['more'], ref: moreMenuRef, subMenu: [
          { id: 'token', text: lang['token'], link: `https://deri.io/#/token` },
          { id: 'stats', text: lang['stats'], link: 'https://info.deri.io/#/info' },
          { id: 'airdrop', text: lang['airdrop'], link: `https://deri.io/#/airdrop` },
          // {id:'compensation',text:lang['deri-compensation-plan'],link:`https://deri.io/#/compensation`},
          { id: 'governance', text: lang['governance'], link: `https://deri.io/#/governance` },
          { id: 'testnet', text: lang['testnet'], link: 'https://testnet.deri.io' },
          { id: 'promotional-event', text: lang['promotional-event'], link: `https://deri.io/#/trade-to-earn` },
          { id: 'retired-pool', text: lang['retired-pool'], link: `https://deri.io/#/retired` },
        ]
      },
      {
        id: 'eoc', text: lang['eoc'], link: `https://deri.fi/#/betit`, target: true, tip: "deri.fi", tipHtml: true, element: <div className="deri-fi-box">
          <div className='deri-fi-text'>
            <div className='deri-fi-logo-title'>
              <Icon token="deri-fi-logo" />
              DERI.FI
          </div>
            <div className='deri-fi-connect'>
              PORTAL FOR ALL DERI PROJECTS
          </div>
          </div>
          <div className="bg-color-box"></div>
        </div>
      },
      {
        id: 'eoc', text: lang['deri-lite'], link: `https://lite.deri.io`, target: true, tip: "deri lite", tipHtml: true, element: <div className="deri-fi-box deri-lite-box">
          <div className='deri-fi-text'>
            <div className='deri-fi-logo-title'>
              <Icon token="deri-lite-logo" />
              DERI LITE
          </div>
            <div className='deri-fi-connect'>
            SIMPLE UX TO GET ON BOARD
          </div>
          </div>
          <div className="bg-color-box"></div>
        </div>
      },
    ]
  ), [locale])
  if (Env === 'testnet' || Env === 'dev') {
    menus.splice(4, 1)
    menus.splice(5, 1)
    menus.splice(5, 1)
    menus = menus.concat([
      { id: 'faucet', text: lang['faucet'], link: 'https://testnet.deri.io/#/faucet', isTestnet: true },
      // { id: 'practice', text: lang['practice-to-earn'], icon: <Icon width='16' height='20' token = 'fire' />, link: '/practice-to-earn', className: 'long' }
      {
        id: 'eoc', text: lang['eoc'], link: `https://deri.fi/#/betit`, target: true, tip: "deri.fi", tipHtml: true, element: <div className="deri-fi-box">
          <div className='deri-fi-text'>
            <div className='deri-fi-logo-title'>
              <Icon token="deri-fi-logo" />
              DERI.FI
          </div>
            <div className='deri-fi-connect'>
              PORTAL FOR ALL DERI PROJECTS
          </div>
          </div>
          <div className="bg-color-box"></div>
        </div>
      },
    ])
  }




  // useEffect(() => {
  //   if(wallet.supportOptions){
  //     if(!menus.some(menu => menu.id === 'options')) {
  //       menus.splice(2,0,options)
  //     }
  //   } else {
  //     if(menus.some(menu => menu.id === 'options')) {
  //       menus.splice(2,1)
  //     }
  //   }
  //   if(wallet.supportPowers){
  //     if(!menus.some(menu => menu.id === 'powers')) {
  //       menus.splice(2,0,powers)
  //     }
  //   } else {
  //     if(menus.some(menu => menu.id === 'powers')) {
  //       menus.splice(2,1)
  //     }
  //   }
  // },[wallet.detail])

  return (
    <div className='menu'>
      <div className='back-icon' onClick={() => closeSideMenu(false)}>
        <Icon token='back' width='16' height='16' />
      </div>
      {menus.map(menu => {
        return (
          <span className={`menu-item ${menu.id}-item`} key={menu.id} onClick={() => !menu.subMenu && closeSideMenu(false)}>
            {menu.subMenu
              ?
              <SubMenu menu={menu} closeSideMenu={closeSideMenu} />
              :
              <>{menu.icon}
                {/* {/^https:\/\//.test(menu.link) 
                ? <a href={menu.link}>{menu.text}</a>
                : <Link to={menu.link}>{menu.text}</Link>} */}
                {isSingleDomain()
                  ? <Link to={menu.link.replace(/^https?.+#/, '')}> {menu.tip ? <UnderlineText tip={menu.tip} html={menu.tipHtml} element={menu.element}>
                    {menu.text} {menu.target ? <Icon token="target" /> : ''}
                  </UnderlineText> : `${menu.text} ${menu.target ? <Icon token="target" /> : ''}`}
                  </Link>
                  : <a rel="noreferrer" href={menu.link} target={menu.target ? "_blank" : ""}>
                    {menu.tip ? <UnderlineText tip={menu.tip} html={menu.tipHtml} element={menu.element}>
                      {menu.text} {menu.target ? <Icon token="target" /> : ''}
                    </UnderlineText> : `${menu.text} ${menu.target ? <Icon token="target" /> : ''}`}
                  </a>}
              </>
            }
          </span>

        )
      })}
      <Content />
    </div>
  )
}

function SubMenu({ menu, closeSideMenu }) {
  const [isTooltipOpen, setTooltipOpen] = useState(false)
  const tooltipWrapperRef = useRef(null)

  const calcPosition = (position, currentEvent, currentTarget, refNode) => {
    const rect = currentTarget.getBoundingClientRect();
    const top = rect.top + rect.height + 20;
    return { top: top, left: rect.left }
  }

  const hideTooltipHandler = useCallback(() => {
    const tooltipWrapperEl = tooltipWrapperRef.current
    if (tooltipWrapperEl) {
      ReactTooltip.hide(tooltipWrapperEl)
      setTooltipOpen(false)
      document.removeEventListener("click", hideTooltipHandler)
    }
  }, [])

  const onTooltipTriggerClick = useCallback(() => {
    const tooltipWrapperEl = tooltipWrapperRef.current
    if (tooltipWrapperEl) {
      if (isTooltipOpen) {
        ReactTooltip.hide(tooltipWrapperEl)
        setTooltipOpen(false)
      } else {
        ReactTooltip.show(tooltipWrapperEl)
        setTooltipOpen(true)
        setTimeout(() => {
          document.addEventListener("click", hideTooltipHandler)
        }, 0)
      }
    }
  }, [isTooltipOpen])

  useEffect(() => {
    return () => {
      document.removeEventListener("click", hideTooltipHandler)
    }
  }, [])
  return (
    <>
      <div className='sub-item'
        data-tip
        data-for={menu.id}
        ref={tooltipWrapperRef}
        data-event="click"
        data-event-off=''
        onClick={onTooltipTriggerClick}>
        {menu.text}
        <Icon token={isTooltipOpen ? 'arrow-up' : 'arrow-down'} width='20' height='20' />
      </div>
      <Tooltip id={menu.id}
        overridePosition={calcPosition}
        scrollHide={false}
        isCapture
        clickable >
        {
          menu.subMenu.map(subMenu => <div className={`sub-menu-item ${subMenu.id}-item`} key={subMenu.id}> <a href={subMenu.link} onClick={closeSideMenu} rel='noreferrer' alt=''>{subMenu.text}</a>
          </div>)
        }
      </Tooltip>
      {menu.icon}
    </>
  )
}

export default inject('wallet')(observer(Menu))