import { getWeb3 } from '../../v3/utils/web3';
import { DEBUG } from '../config';
import { AppError } from '../error/error';
import { web3Factory } from '../factory/web3';
import { numberToHex } from '../utils/convert';

const MAX_GAS_AMOUNT = 812731 * 5;
// const RE_ERROR_MSG = /\"message\":\s\"execution\sreverted:([\w\s]+)\"/
const RE_ERROR_MSG = /(\{[\s\S]+\})/

const noOp = () => {}

export class ContractBase {
  constructor(chainId, contractAddress, contractAbi) {
    this.chainId = chainId;
    this.contractAddress = contractAddress;
    this.contractAbi = contractAbi;
  }

  async _init(isTx=false) {
    // re-init web3 and contract when web3 instance is null
    if (!this.web3) {
      if (!!this.useProvider) {
        this.web3 = await getWeb3(this.chainId);
      } else {
        this.web3 = await web3Factory.get(this.chainId, isTx);
      }
      // this.web3.eth.handleRevert = true
      if (this.web3) {
        this.contract = new this.web3.eth.Contract(
          this.contractAbi,
          this.contractAddress
        );
        this.contract.transactionConfirmationBlocks = 3;
        this.contract.transactionPollingTimeout = 300;
        this.contract.transactionPollingInterval = 300;
      }
    }
  }

  async _call(method, args = []) {
    let res;
    let retry = 1;
    // clear web3 cache when using inject provider
    if (this.web3 && !this.web3.currentProvider.httpsAgent) {
      delete this.web3
    }
    while (retry > 0) {
      try {
        await this._init();
        if (this.web3) {
          res = await this.contract.methods[method](...args).call();
          return res;
        }
      } catch (err) {
        DEBUG && console.log(err);
        // throw err
        // remove web3 instance to re-init
        if (retry === 1) {
          delete this.web3
          web3Factory.clean(this.chainId);
        }
      } finally {
        retry -= 1;
      }
    }
    throw new AppError("JSON_RPC_CALL_TIMEOUT", {
      name: method,
      args: [...args, this.contractAddress],
    });
  }

  async _estimatedGas(method, args = [], accountAddress, opts, onWarning) {
    // await this._init();
    let gas = 0;
    for (let i = 0; i < 2; i++) {
      try {
        gas = await this.contract.methods[method](...args).estimateGas({
          from: accountAddress,
          ...opts,
        });
        gas = parseInt(gas * 1.25);
        break;
      } catch (error) {
        // console.log(`-- estimatedGas: ${error}`)
        const res = error.toString().match(RE_ERROR_MSG);
        if (Array.isArray(res) && res.length >= 2) {
          const errorObject = JSON.parse(res[1]);
          const msg =
            (errorObject.message &&
              errorObject.message.replace("execution reverted: ", "")) ||
            "";
          onWarning(msg);
        }
      }
    }
    if (gas === 0) gas = MAX_GAS_AMOUNT;
    return gas;
  }

  _getTransactionReceipt(tx) {
    const self = this;
    return function _transactionReceipt(resolve, reject) {
      self.web3.eth.getTransactionReceipt(tx, (error, receipt) => {
        if (error) {
          reject(error);
        } else if (receipt === null) {
          setTimeout(() => _transactionReceipt(resolve, reject), 100);
        } else if (receipt.status === false) {
          //receipt.errorMessage = 'Transaction failed';
          reject(receipt);
        } else {
          resolve(receipt);
        }
      });
    };
  }

  async _transact(method, args = [], accountAddress, opts = {}) {
    // clean web3 instance
    delete this.web3;
    await this._init(true);
    let restOpts = {};
    let { onAccept, onReject, onWarning, onReceipt, value } = opts;

    // set default value if callback is empty
    if (!onAccept) {
      onAccept = noOp;
    }
    if (!onReject) {
      onReject = noOp;
    }
    if (!onWarning) {
      onWarning = noOp;
    }
    if (!onReceipt) {
      onReceipt = noOp;
    }
    // if no web3 instance, close the dialog
    if (!this.web3) {
      console.log(`-- cannot init web3 instance: chainId(${this.chainId})`)
      onReceipt()
    }

    // for ETH/BSC transfer
    if (value) {
      restOpts.value = numberToHex(value);
    }
    // if (nonce) {
    //   restOpts.nonce = numberToHex(nonce);
    // }

    let [gas] = await Promise.all([
      this._estimatedGas(method, args, accountAddress, { value }, onWarning),
      // this.web3.eth.getGasPrice(),
      // this.web3.eth.getTransactionCount(accountAddress),
    ]);

    let res;
    let txRaw = [
      {
        from: accountAddress,
        gas: numberToHex(gas),
        // gasPrice: numberToHex(gasPrice),
        // nonce: numberToHex(nonce),
        ...restOpts,
        data: this.contract.methods[method](...args).encodeABI(),
        to: this.contractAddress,
      },
    ];
    try {
      let txHash = await window.ethereum.request({
        method: 'eth_sendTransaction',
        params: txRaw,
      });
      onAccept(txHash);
      res = await new Promise(this._getTransactionReceipt(txHash));
    } catch(error) {
      console.error(error)
      if (error.code === 4001) {
        // user reject
        onReject();
      }
      if (onReceipt && typeof onReceipt === "function") {
        onReceipt();
      }
      throw error
    }
    // let txRaw = [
    //   {
    //     from: accountAddress,
    //     gas: numberToHex(gas),
    //     gasPrice: numberToHex(gasPrice),
    //     nonce: numberToHex(nonce),
    //     ...restOpts,
    //     data: this.contract.methods[method](...args).encodeABI(),
    //     to: this.contractAddress,
    //   },
    // ];
    // try {
    //   let txHash = await window.ethereum.request({
    //     method: 'eth_sendTransaction',
    //     params: txRaw,
    //   });
    //   onAccept(txHash);
    //   res = await new Promise(this._getTransactionReceipt(txHash));
    // } catch(error) {
    //   console.error(error)
    //   if (error.code === 4001) {
    //     // user reject
    //     onReject();
    //   }
    //   if (onReceipt && typeof onReceipt === "function") {
    //     onReceipt();
    //   }
    //   throw error
    // }
    return res
  }
}
