import { getSpecification, DeriEnv, bg } from "../lib/web3js/index";
import { makeObservable, observable, action, computed, makeAutoObservable } from "mobx";
import Intl from "./Intl";
import version from "./Version";
import type from "./Type";
import { getDefaultNw } from "../utils/utils";
import ApiProxy from "./ApiProxy";
import { SHORT_NAME_MAP } from "../utils/Constants";
import WebSocket from '../model/WebSocket'
const WS_URL = process.env.REACT_APP_BACKEND_WSS_URL
export default class Contract {
  info = {}
  ws = new WebSocket(WS_URL,{    
    onConnectError : () => this.getDataFromRest(),    
    onReconnect : () => this.getDataFromWs(),
    onDisconnect : () => this.subscribed = false
  })
  

  load(wallet,symbolInfo,cb){
    if(symbolInfo){
      const chainId = wallet && wallet.isConnected() ? wallet.detail.chainId : getDefaultNw(DeriEnv.get()).id
      this.chainId = chainId
      this.symbolInfo = symbolInfo
      this.cb = cb      
      symbolInfo.isAllV3 ? this.getDataFromWs() : this.getDataFromRest()
      this.getDataFromRest()
    }
  }

  async getDataFromWs(){
    if(!this.subscribed && this.symbolInfo) {
      const args =[this.chainId,this.symbolInfo.address,this.symbolInfo.symbol]
      const channel = `symbol_spec.${args.join('.')}`      
      this.ws.subscribe('add_channel',{
        api : 'symbol_spec',args : args
      },data => {
        if(this.symbolInfo.symbol === data.symbol){          
          this.cb && this.cb(this.wrapInfo(data))
        }
      },channel,false)
      this.subscribed = true
    }
  }

  clean(){
    this.unSubscribe()
    // this.ws.disconnect()
  }

  unSubscribe(){
    if(this.subscribed && this.symbolInfo){
      this.ws.unsubscribe('remove_channel',{
        api : 'symbol_spec',args : [this.chainId,this.symbolInfo.address,this.symbolInfo.symbol]
      })
      this.subscribed = false
    }
  }

  async getDataFromRest(){
    if(this.symbolInfo){
      const symbolParam = this.symbolInfo.isAllV3 ? this.symbolInfo.symbol : this.symbolInfo.symbolId
      const info = await ApiProxy.request('getSpecification',[this.chainId,this.symbolInfo.address,symbolParam]);
      this.cb && this.cb(this.wrapInfo(info))
    }
  }

  wrapInfo(spec){
    spec.bTokenSymbol = this.bTokenSymbolDisplay(spec)
    spec.minTradeUnit = this.symbolInfo.isPower && this.symbolInfo.formattedUnit ? bg(spec.multiplier).div(SHORT_NAME_MAP[this.symbolInfo.formattedUnit]).toNumber() : spec.multiplier
    spec.displayMultiplier = this.symbolInfo.isPower && this.symbolInfo.formattedUnit ? SHORT_NAME_MAP[this.symbolInfo.formattedUnit] : spec.multiplier
    spec.displaySymbol = this.symbolInfo.displaySymbol || spec.symbol
    if(type.isOption && spec && spec.symbol){
      Object.assign(spec,this.extractBySymbol(spec))
    }
    return spec
  }

  extractBySymbol(spec){
    const symboleInArray = spec.symbol.split('-')
    const underlier = symboleInArray[0]
    const strike = symboleInArray[1]
    const optionType = symboleInArray[2]
    return {
      underlier,
      strike,
      optionType : optionType === 'C' ? 'Call' : 'Put'
    }
  }

  bTokenSymbolDisplay(spec){
    if(!Array.isArray(spec.bTokenSymbol)){
      return [spec.bTokenSymbol];
    }
    return spec.bTokenSymbol;
  }

}