import { bg } from '../../../../utils/utils';
import {
  catchApiError,
  fetchJson,
  fromWei,
  getBlockInfo,
  getHttpBase,
  getPastEvents,
} from '../../shared/utils';
import { poolImplementationFactory } from '../contract/factory/pool';
import { deriSymbolScaleIn, deriSymbolScaleOut, isPowerSymbol, normalizeDeriSymbol } from '../utils/power';

export const getTradeHistory = async (chainId, poolAddress, accountAddress) => {
  return catchApiError(async () => {
    let tradeHistoryBlock = 0,
      tradeHistory = [];
    const url = `${getHttpBase()}/trade_history/${chainId}/${poolAddress}/${accountAddress}/0`;
    const res = await fetchJson(url);
    if (res && res.success && res.data) {
      tradeHistoryBlock = parseInt(res.data.tradeHistoryBlock);
      tradeHistory = res.data.tradeHistory;
    }
    if (tradeHistory.length > 0) {
      tradeHistory = tradeHistory.map((i) => {

      const notional = isPowerSymbol(i.symbol)
        ? bg(i.indexPrice).times(i.indexPrice).times(i.volume).toString()
        : bg(i.indexPrice).times(i.volume).toString();

        return {
          direction: i.direction.trim(),
          baseToken: '',
          symbolId: '',
          symbol: i.symbol,
          displaySymbol: normalizeDeriSymbol(i.symbol),
          price: deriSymbolScaleIn(i.symbol, i.price),
          indexPrice: i.indexPrice || '-',
          notional: notional,
          volume: deriSymbolScaleOut(i.symbol, i.volume),
          transactionFee: i.transactionFee,
          transactionHash: i.transactionHash,
          time: i.time.toString(),
        };
      });
    }
    if (tradeHistoryBlock !== 0) {
      const tradeHistoryOnline = await getTradeHistoryOnline(
        chainId,
        poolAddress,
        accountAddress,
        tradeHistoryBlock + 1
      );
      const result = tradeHistoryOnline.concat(tradeHistory);
      return result.sort((a, b) => parseInt(b.time) - parseInt(a.time));
    } else {
      // trade history is not sync
      return []
    }
  });
};

const getTradeHistoryOnline = async (
  chainId,
  poolAddress,
  accountAddress,
  fromBlock
) => {
  const pool = poolImplementationFactory(chainId, poolAddress);
  await pool.init();
  const pTokenId = await pool.pToken.getTokenIdOf(accountAddress);
  const toBlock = await getBlockInfo(chainId, 'latest');
  fromBlock = parseInt(fromBlock);
  const filters = { pTokenId };
  //console.log(fromBlock, toBlock.number, filters);
  let result = [];
  await pool.symbolManager._init();
  try {
    let events = await getPastEvents(
      chainId,
      pool.symbolManager.contract,
      'Trade',
      filters,
      fromBlock,
      toBlock.number
    );
    for (let event of events) {
      const res = await pool.symbolManager.formatTradeEvent(event, pool.symbols, accountAddress);
      result.push({
        direction: res.direction.trim(),
        baseToken: "",
        symbolId: "",
        symbol: res.symbol,
        displaySymbol: normalizeDeriSymbol(res.symbol),
        price: res.price,
        indexPrice: res.indexPrice || "-",
        notional: res.notional,
        volume: res.volume,
        transactionFee: res.transactionFee,
        transactionHash: res.transactionHash,
        time: res.time.toString(),
      });
    }
  } catch (err) {
    console.log(err)
  }
  return result
};
