import { DEBUG } from "../config";

export const factory = (klass) => {
  let instances = {}
  return (chainId, address) => {
    const key = `${chainId}_${address}`
    if (Object.keys(instances).includes(key)) {
      return instances[key];
    } else {
      instances[key] = new klass(chainId, address);
      return instances[key];
    }
  }
}

// add use provider support
export const factoryWithOpts = (klass) => {
  let instances = {}
  return (chainId, address, opts = {}) => {
    const key = `${chainId}_${address}${opts.useProvider ? '_provider' : ''}`
    DEBUG && console.log(`-- contractFactoryWithOpts: ${key}`)
    if (Object.keys(instances).includes(key)) {
      return instances[key];
    } else {
      instances[key] = new klass(chainId, address, opts);
      return instances[key];
    }
  }
}